<template>
  <div class="container-auth">
    <h2 class="title">Register</h2>
    <form class="register-form" @submit.prevent="register">
      <div class="input-field">
        <input v-model="username" type="text" placeholder="Enter User Name" />
        <i class="fa fa-envelope"></i>
      </div>
      <div class="input-field">
        <input v-model="email" type="email" placeholder="Enter Your Email" />
        <i class="fa fa-lock"></i>
      </div>
      <div class="input-field">
        <input v-model="password" type="password" placeholder="Create Password" />
        <i class="fa fa-key"></i>
      </div>

      <div class="input-field">
        <input v-model="confirmPassword" type="password" placeholder="Confirm Password" />
        <i class="fa fa-key"></i>
      </div>

      <router-link class="write-page-link" to="/login"
        >Have An Acount ? <span>Login</span></router-link
      >
      <button type="submit" class="btn submit">Register</button>
    </form>
  </div>
</template>

<script>
import "./auth.css"; 
import {mapState} from "vuex";
import axios from "axios";
export default {
  data(){
    return {
      username:null , 
      email: null ,
      password :null , 
      confirmPassword :null , 
    }

  } ,
  methods: {
    async register (){
      try {
        const res = await axios.post("http://localhost:4000/api/auth/register" , {username:this.username , email:this.email , password:this.password}  ,{ withCredentials: true }) ; 
       console.log(res.data); 
       document.location.href="/"
       
      } 
      catch(err){
        console.log(err);
      }
    }

  }, 
  computed :{
      ...mapState(["showNotificationError"]) ,
    } ,

};
</script>
