<template>
  <nav v-if="!user" >
      <div class="container">
        <div class="left">
          <router-link to="/" class="logo">
            <h2>
              Travel <span class="active">Aid</span>
            </h2>
          </router-link>
        </div>
        <div class="right">
          <ul class="list-auth">
            <li>
              <router-link to="/login">Login</router-link>
            </li>
            <li>
              <router-link to="/register">Register</router-link>
            </li>
          </ul>
        </div>
      </div>

  </nav>
  <nav v-if="user">
    <div class="container">
      <div class="left">
          <router-link to="/" class="logo">
            <h2>
              Travel <span class="active">Aid</span>
            </h2>
          </router-link>
        </div>   
        <div class="center">
        <ul>
        <li>
        <router-link to="/">
        <i class="fa fa-home"></i>
        </router-link>
        </li> 
         <li>
        <router-link to="/message">
        <i class="fa fa-comment"></i>
        </router-link>
        </li> 
         <li>
        <router-link to="/notification">
          <i class="fa fa-bell"></i>
        </router-link>
        </li> 
         <li>
        <router-link to="/setting">
        <i class="fa fa-cog"></i>
        </router-link>
        </li>
        </ul>
        </div>
      <div class="right">
      <div class="user-info">
      <img src = "../../assets/img/profilepic.png" class="med-pic" >
      <h4>{{user.username}}</h4>

      </div>
      <div class="right-model"> 
      <i class="fa fa-caret-down arrow" @click="toggleMenu=!toggleMenu" ></i>
      <ul class="right-model-menu" v-if="toggleMenu">
      <li>
      <router-link to="/login" @click="logout">logout</router-link>
      </li>
            <li>
            <router-link to="/profile">Profile Page</router-link>
      </li>

      </ul>

      </div>
      </div>
    </div>
  </nav>

</template>

<script>
import {mapState  } from "vuex";
import axios from "axios";
import "./header.css"
export default {
  name: 'navigation',
  data(){
    return {
      toggleMenu:false, 
    }
  } ,
  methods:{
   async logout(){
      try {
         await axios.get("http://localhost:4000/api/auth/logout" , { withCredentials: true }) ;
         document.location.href="/";

      } 
      catch (err){
        console.log(err.response.data);

      }
    }
    } ,   
  computed:{
    ...mapState(["user","test" ]),
  } ,    

}
</script>
<style scoped>
.logo{
  color:#000;
}
.logo .active{
  color:var(--main-color); 
}
</style>